.socialmedia_container{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.socialmedia_link_spot{
    width: 35%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: grey;
    margin-left: 50%;
    font: outline;
}

@media only screen and (max-width: 600px) {
    .socialmedia_container .socialmedia_link_spot{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 0;
    }
}